// @flow
import React from 'react';
import styled from 'styled-components';

import type { AppPageProps } from './pageLib';

const NoteListPageDiv = styled.div`
	text-align: center;
`;

class NoteListPage extends React.Component<AppPageProps> {
	componentWillMount = () => {
		if (this.props.setTitle) {
			this.props.setTitle('Current Notes');
		}
	};

	noteStrings = [
		'C#',
		'HTML',
		'Python',
	];
	noteDOM = this.noteStrings.map((str, index) => (
		<div className="tile is-child is-12 box" key={index}>{str}</div>
	));

	render = () => {
		return (
			<NoteListPageDiv className="NoteListPage">
				<div className="NoteList tile is-ancestor">
					<div className="tile is-parent is-4 is-vertical" />
					<div className="tile is-parent is-4 is-vertical">
						{this.noteDOM}
					</div>
				</div>
			</NoteListPageDiv>
		);
	};
}

export default NoteListPage;



