// @flow
import * as React from 'react';
import styled from 'styled-components';

import NoteTable from '../NoteTable/NoteTable';
import InputForm from '../InputForm';
import type { AppPageProps } from './pageLib';
import { DETAIL_INDEX, STARRED_INDEX, TITLE_INDEX } from '../NoteDataIndexes';

const cloneDeep = require('lodash/cloneDeep');

type State = {
	filterStr: string,
	selected: ?number,
	noteData: { [number]: [string, string, boolean]},
	nextId: number,
};

const ActionBar = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
`;

const ActionBarItem = styled.div`
	margin-left: 0.5em;
	margin-right: 0.5em;
`;

class NotePage extends React.Component<AppPageProps, State> {
	/* eslint-disable no-template-curly-in-string */
	/* eslint-disable no-useless-computed-key */
	state = {
		filterStr: '',
		selected: null,
		noteData: {
			[2]: ['Clear Screen', '# Ctr-L', false],
			[1]: ['End Standard Input', '# Ctr-D', false],
			[0]:
				['while',
					`\`\`\`
while [ $x != bye]
	do
	read x
done
\`\`\``,
					false
				],
			[3]: ['String Length', '{#mystr}', false],
			[4]: ['Substring', '${my_str:offset:length}', false],
			[5]:
				['if',
					`# if block
\`\`\`bash
if [ "$x"=bar ];
	then ...;
fi
\`\`\`

- Spacing matters. \`[ "$for"=bar ];\` is correct
- This tests string equality. Need -eq for numbers

# if shorthand

\`\`\`bash
[ $x"=bar ] && echo true || echo false
\`\`\`

# if <, <=, =, >=, >
\`\`\`
-le, -le, -eq, -ge, -gt
\`\`\`


# if non-zero length
\`\`\`bash
[ -n "$myStr" ]
\`\`\`
Needs quotes, otherwise returns true for empty vars.

# if file exists
\`\`\`bash
[ -e fileName ]
\`\`\`

`,
					false
				],
		},
		nextId: 6,
	};
	/* eslint-enable no-template-curly-in-string */
	/* eslint-enable no-useless-computed-key */

	componentWillMount = () => {
		if (this.props.setTitle) {
			this.props.setTitle('Notes Page');
		}
	};

	setFilterStr = (filterStr: string) => this.setState({ filterStr: filterStr.toLowerCase() });

	setSelected = (id: ?number) => {
		this.setState({ selected: id });
	};

	toggleStar = () => {
		if (this.noteTable && this.state.selected) {
			const key = this.state.selected;
			const starred = this.state.noteData[key][STARRED_INDEX];
			// Clone and edit noteData
			const noteDataCopy = cloneDeep(this.state.noteData);
			noteDataCopy[key][STARRED_INDEX] = !starred;
			this.setState({ noteData: noteDataCopy, selected: null });
		}
	};

	deleteItem = () => {
		if (this.noteTable) {
			const key = this.state.selected;
			if (key) {
				this.noteTable.deleteSubtitlesForDetail(key);
				const noteDataCopy = cloneDeep(this.state.noteData);
				delete noteDataCopy[key];
				this.setState({ noteData: noteDataCopy, selected: null });
			}
		}
	};

	addItem = () => {
		if (this.noteTable) {
			const noteDataCopy = cloneDeep(this.state.noteData);
			noteDataCopy[this.state.nextId] = ['', 'Default Element'];
			this.setState({
				noteData: noteDataCopy,
				nextId: this.state.nextId + 1,
			});
		}
	};

	saveDetail = (id: ?number, detail: string) => {
		if (id) {
			const noteDataCopy = cloneDeep(this.state.noteData);
			noteDataCopy[id][DETAIL_INDEX] = detail;
			this.setState({ noteData: noteDataCopy });
		}
	};

	updateTitleValue = (key: number, newValue: string) => {
		const noteDataCopy = cloneDeep(this.state.noteData);
		noteDataCopy[key][TITLE_INDEX] = newValue;
		this.setState({ noteData: noteDataCopy });
	};

	noteTable: ?NoteTable;

	render = () => {
		return (
			<div className="NotePage">
				<ActionBar className="NotePage__SearchBar NoteSearchBar">
					<span className="NoteSearchBar__Item">Filter: </span>
					<ActionBarItem>
						<InputForm value={this.state.filterStr} updateValue={this.setFilterStr} />
					</ActionBarItem>
					<ActionBarItem>
						<button className="button is-primary" onClick={this.addItem}>Add</button>
					</ActionBarItem>
					<ActionBarItem>
						<button className="button is-primary"
							onClick={this.deleteItem}
							disabled={this.state.selected === null}
						>
							Delete
						</button>
					</ActionBarItem>
					<ActionBarItem>
						<button className="button is-primary"
							disabled={this.state.selected === null}
							onClick={this.toggleStar}
						>
							{this.state.selected &&
							this.state.noteData[this.state.selected][STARRED_INDEX] ?
								'Un-Star' : 'Star'
							}
						</button>
					</ActionBarItem>
				</ActionBar>
				<NoteTable
					ref={(noteTable) => { this.noteTable = noteTable; }}
					filterStr={this.state.filterStr}
					selected={this.state.selected}
					noteData={this.state.noteData}
					saveDetail={this.saveDetail}
					setSelected={this.setSelected}
					updateTitleValue={this.updateTitleValue}
				/>
			</div>
		);
	};
}

export default NotePage;



