// @flow
import React from 'react';
import styled from 'styled-components';

import type { AppPageProps } from './pageLib';

const AboutPageDiv = styled.div`
	text-align: center;
`;

class AboutPage extends React.Component<AppPageProps> {
	componentWillMount = () => {
		if (this.props.setTitle) {
			this.props.setTitle('App by Chris Leong');
		}
	};

	render = () => {
		return (
			<AboutPageDiv>
				<p><strong>Built with:</strong></p>
				<ul>
					<li>Babel</li>
					<li>Bulma</li>
					<li>Create React App</li>
					<li>CSS3</li>
					<li>ESLint (+Babel ESLint, Plugin React, Plugin Flowtype)</li>
					<li>Font Awesome</li>
					<li>Flow</li>
					<li>Javascript (ES6)</li>
					<li>JSX</li>
					<li>HTML5</li>
					<li>Node</li>
					<li>React</li>
					<li>React-Router</li>
					<li>Stylelint</li>
					<li>Styled Components</li>
					<li>Yarn</li>
				</ul>
				<br />
				<p><strong>With the assistance of:</strong></p>
				<ul>
					<li>DevDocs</li>
					<li>Emmet</li>
					<li>Firefox Dev Tools</li>
					<li>Git</li>
					<li>iTerm 2</li>
					<li>Source Tree</li>
					<li>WebStorm</li>
				</ul>
			</AboutPageDiv>
		);
	};
}

export default AboutPage;



