// @flow
import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import './lib/bulma_styles.css';
import NotePage from './components/Pages/NotePage';
import AboutPage from './components/Pages/AboutPage';
import NoteListPage from './components/Pages/NoteListPage';
import Header from './components/Pages/Header';


class App extends React.Component<{}, { title: string }> {
	state = { title: 'Hello World' };

	setTitle = (title: string) => {
		if (this.state.title !== title) {
			this.setState({ title });
		}
	};

	render = () => (
		<Router>
			<div className="App">
				<Header title={this.state.title + "******"} />
				<section className="App__main section">
					<Switch>
						<Route exact path="/" render={
							() => <NoteListPage setTitle={this.setTitle} />
						}
						/>
						<Route exact path="/about" render={
							() => <AboutPage setTitle={this.setTitle} />
						}
						/>
						<Route exact path="/note/:id" render={
							() => <NotePage setTitle={this.setTitle} />
						}
						/>
					</Switch>
				</section>
			</div>
		</Router>
	);
}


export default App;



