// @flow
import styled from 'styled-components';
import ReactMarkdown from 'react-markdown';

const StyledMarkdown = styled(ReactMarkdown)`
	& h1 {
		font-weight: bold;
		
		& :not(:first-child) {
			margin-top: 1em;
		}
	}

	& li {
		list-style: disc inside none;
	}

	& code {
		color: inherit;
	}
`;

export default StyledMarkdown;



