// @flow
import * as React from 'react';
import Highlighter from 'react-highlight-words';


type HeadingProps = {
	level: number,
	children?: React.ChildrenArray<*>,
};

const getHeaderRenderer = (filterStr: string) => {
	class HeadingRenderer extends React.Component<HeadingProps> {
		render = () => {
			if (this.props.level === 1) {
				if (this.props.children && React.Children.count(this.props.children) === 1) {
					if (typeof this.props.children[0] === 'string') {
						const tmp = this.props.children[0];
						return (
							<h1>
								<Highlighter
									highlightClassName="search-highlight"
									searchWords={[ filterStr ]}
									textToHighlight={tmp}
									autoescape
								/>
							</h1>
						);
					}
				} else {
					return (
						<h1>{this.props.children || ''}</h1>
					);
				}
			}
			// Default
			return <span> Case Not Handled </span>;
		};
	}
	return HeadingRenderer;
};

export default getHeaderRenderer;



