// @flow
import React from 'react';

import { default as styled, injectGlobal } from 'styled-components';

import { NavLink } from 'react-router-dom';

export type HeaderProps = {
	title: string
};

const TopSecLeft = styled.div`
	position: absolute;
	margin-left: 1em;
`;

const TopSecCenter = styled.div`
	margin-left: auto;
	margin-right: auto;
	
	& h1 {
		text-align: center;
	}
`;

const MainNav = styled.div`margin-left: 1em;`;

injectGlobal`
	.nav-link--selected {
		font-weight: bold;
	}
`;


/* Uses NavLinks, so needs to occur in a router */
const Header = (props: HeaderProps) => (
	<header className="App__Header Header">
		<div className="Header__Top-Sec Top-Sec">
			<TopSecLeft>
				<div>
					<h1 className="title is-2">ProgNotes</h1>
				</div>
			</TopSecLeft>
			<TopSecCenter>
				<h1 className="title is-1">{props.title}</h1>
			</TopSecCenter>
		</div>
		<nav className="Header__Nav level is-mobile">
			<MainNav className="level-left">
				<div className="level-item">
					<NavLink to="/" activeClassName="nav-link--selected">Home</NavLink>
				</div>
				<div className="level-item">
					<NavLink to="/about" activeClassName="nav-link--selected">About</NavLink>
				</div>
				<div className="level-item">
					<NavLink to="/note/0" activeClassName="nav-link--selected">My Note</NavLink>
				</div>
			</MainNav>
		</nav>
	</header>
);

export default Header;



